import React, { FC } from 'react';
import s from './SubscriptionCard.module.scss';
import Check from '../../images/checkBlue.svg';
import { useNavigate } from 'react-router-dom';
import { CustomButton } from 'components/CustomButton/CustomButton';
import { Subscription } from 'types/types';

interface SubscriptionCardProps {
  subscription: Subscription;
}

export const SubscriptionCard: FC<SubscriptionCardProps> = ({ subscription }) => {
  const {
    id,
    title,
    description,
    info,
    price,
    period,
    discount,
    pros,
  } = subscription;

  const navigate = useNavigate();

  return (
    <article className={s.container}>
      <h2 className={s.title}>{title}</h2>
      <span className={s.description}>{description}</span>
      <p className={s.priceWrapper}>
        <span className={s.price}>{'$' + price}</span>
        <span className={s.period}>{'/' + period}</span>
        {discount && <div className={s.discount}>{`Save ${discount}%`}</div>}
      </p>
      <span className={s.description}>{info}</span>
      <CustomButton
        text='Get started >'
        variant='outline'
        style={{ width: '100%' }}
        imagePos='end'
        onClick={() => navigate(`/payment/${id}`)}
      />
      <div className={s.prosContainer}>
        {pros.map(pro => (
          <div key={pro} className={s.proWrapper}>
            <Check />
            <span>{pro}</span>
          </div>
        ))}
      </div>
    </article>
  );
};
